import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Layout from '../components/Layout'
import { Typography, ButtonBase, DialogTitle } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';

import Recorder from 'recorder-js';
import { recording } from '../scripts/record-script';
import { saveAs } from 'file-saver';


const group = {
    height: "30vh"
}

const buttonStyle = {
    marginLeft: "20px",
    marginRight: "20px"
}

// audio stuff
let isRecording = false;
let blob = null;
let stream, audioContext, audioCtx, mediaRecorder, audioURL;
let chunks = [];


class record extends Component {

    constructor(props) {
        super(props);
        this.state = {
            appIsMounted: false,
            recording: false,
            completedRecording: false,
        };
    }

    

    componentDidMount() {
        requestAnimationFrame(() => {
            this.setState({ appIsMounted: true });
        });

        // initialize stuff for audio recording
        // audioCtx = new (window.AudioContext || webkitAudioContext)();
        const constraints = {audio: true};
        navigator.mediaDevices.getUserMedia(constraints).then(stream => {
            mediaRecorder = new MediaRecorder(stream)
        })

        console.log(this.state)
    }

    recordStart = () => {
        // initialize the audio recorder stuff
        this.setState({recording:true});
        audioContext =  new (window.AudioContext || window.webkitAudioContext)();
        mediaRecorder.start();
    }

    recordStop = () => {
        mediaRecorder.stop();

        this.setState({completedRecording: true})
        this.setState({recording:false});

        mediaRecorder.ondataavailable = function(e) {
            chunks.push(e.data);
            blob = new Blob(chunks, { 'type' : 'audio/ogg; codecs=opus' });
            audioURL = window.URL.createObjectURL(blob);
        }
    }

    recordPlay = () => {
        const a = new Audio(audioURL);
        a.play();
    }

    render() {
        if (this.state.appIsMounted) {
            let s

            return (
                <div>
                    {/* { this.state.appIsMounted && React.createElement(require('recorder-js').default)} */}
                    <Layout>
                        <Paper elevation={"1"}>
                            <Grid container spacing={24}>




                                {/* title and subtitle */}
                                <Grid item xs={12} style={group}>
                                    <Typography variant="h1" component="h3" style={styles}>
                                        Record
                                    </Typography>
                                    <Typography variant="h4" style={styles}>
                                        The interactive speech training suite
                                    </Typography>
                                </Grid>
                                {/* start/stop buttons */}
                                <Grid container xs={12} direction="row" justifyContent="center" alignItems="center" justify="center" alignContent="space-between">
                                    <Button
                                        style={buttonStyle}
                                        variant='contained'
                                        color='secondary'
                                        onClick={this.recordStart}>
                                    Record Sample </Button>
                                    <Button
                                        style={buttonStyle}
                                        variant='contained'
                                        color='secondary'
                                        onClick={this.recordStop}
                                        disabled={!this.state.recording}>
                                    Stop Recording </Button>
                                    <Button
                                        style={buttonStyle}
                                        variant='contained'
                                        color='secondary'
                                        onClick={this.recordPlay}
                                        disabled={!this.state.completedRecording}> 
                                    Play Recording </Button>
                                </Grid>
                                <Grid container xs={12} direction="row" justifyContent="center" alignItems="center" justify="center">
                                    
                                </Grid>
                            </Grid>
                        </Paper>
                    </Layout>
                </div>
            );
        } else {
            return false;
        }
    }
}

export default record